

<template>
	<div>
		<div>{{ `子组件值: ${isShow}` }}</div>
		<div @click="click">点击此处修改值</div>
	</div>
</template>

<script>
	export default {
		name: "TestChildren",
		data() {
			return {
				valueData: ""
			};
		},
		props: {
			isShow: Number
		},
		watch: {
			isShow(newValue) {
				this.valueData = newValue;
				console.log(`子组件值：${newValue}`);
			}
		},
		methods: {
			click() {
				this.valueData++;
				this.$emit("change", this.valueData);
			}
		},
		model: {
			prop: "isShow",
			event: "change"
		},
	};
</script>
