
<template>
	<div>
		<div>{{ `父组件值：${text}` }}</div>
		<div @click="click">点击此处修改值</div>
		<span>---------------------------------</span>
		<test-children v-model="text"></test-children>
	</div>
</template>

<script>
	import TestChildren from "./TestChildren";

	export default {
		name: "TestFather",
		data() {
			return {
				text: 1
			};
		},
		methods: {
			click() {
				this.text--;
			}
		},
		watch: {
			text(newValue) {
				console.log(`父组件值：${newValue}`);
			}
		},
		components: {
			TestChildren,
		},
	};
</script>
